import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import LockOpenIcon from "@material-ui/icons/LockOpen";
import CustomMenu from "./CustomMenu";
import { Link } from "react-router-dom";

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  grow: {
    flexGrow: 1
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20
  },
  icon: {
    marginRight: theme.spacing.unit * 2
  }
});

class CustomAppBar extends React.Component {
  state = {
    anchorEl: null,
    menuOpen: null,
    currRole: "Beneficiary",
    currAccount: { Type: "Ledger", Address: "0x123456" },
    currContract: { Type: "Simple", Address: "0x132435" }
  };

  handleMenu = menuName => event => {
    this.setState({ menuOpen: menuName, anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null, menuOpen: null });
  };

  renderCustomMenu = () => {
    if (this.props.setupComplete === true) {
      return (
        <div>
          <CustomMenu
            allAccounts={this.props.allAccounts}
            allContracts={this.props.allContracts}
            allRoles={this.props.allRoles}
            currMenuIndices={this.props.currMenuIndices}
            setCurrMenuIndex={this.props.setCurrMenuIndex}
          />
        </div>
      );
    } else {
      return <div />;
    }
  };

  render() {
    const { classes } = this.props;
    return (
      <AppBar position="relative">
        <Toolbar>
          <LockOpenIcon className={classes.icon} />

          <Typography variant="h6" color="inherit" className={classes.grow}>
            <Link to={"/"} style={{ color: "inherit", textDecoration: "none" }}>
              SecretKeeper{" "}
            </Link>
          </Typography>
          {this.renderCustomMenu()}
        </Toolbar>
      </AppBar>
    );
  }
}

CustomAppBar.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(CustomAppBar);
