import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Grow from "@material-ui/core/Grow";
import Paper from "@material-ui/core/Paper";
import Popper from "@material-ui/core/Popper";
import MenuItem from "@material-ui/core/MenuItem";
import MenuList from "@material-ui/core/MenuList";

const styles = {
  root: {
    flexGrow: 1
  },
  grow: {
    flexGrow: 1
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20
  }
};

function shortAddr(addr) {
  return addr.substring(0, 8);
}

class CustomMenuItem extends React.Component {
  render() {
    return (
      <Fragment>
        <Button
          aria-owns={
            this.props.menuOpen === this.props.menuName
              ? this.props.menuName
              : undefined
          }
          aria-haspopup="true"
          onClick={this.props.handleMenu(this.props.menuName)}
          color="inherit"
        >
          {this.props.renderSelected()}
        </Button>
        <Popper
          open={this.props.menuOpen === this.props.menuName}
          anchorEl={this.props.anchorEl}
          transition
          disablePortal
        >
          {({ TransitionProps, placement }) => (
            <Grow
              {...TransitionProps}
              id={this.props.menuName}
              style={{
                transformOrigin:
                  placement === "bottom" ? "center top" : "center bottom"
              }}
            >
              <Paper>
                <ClickAwayListener onClickAway={this.props.handleClose}>
                  {this.props.renderMenuList}
                </ClickAwayListener>
              </Paper>
            </Grow>
          )}
        </Popper>
      </Fragment>
    );
  }
}

class CustomMenu extends React.Component {
  state = {
    anchorEl: null,
    menuOpen: null
  };

  renderMenuList = (menuName, menuItems, renderFunc) => {
    return (
      <MenuList>
        {menuItems.map((item, index) => (
          <MenuItem
            key={index}
            onClick={this.handleMenuItemClick(menuName, index)}
            selected={index === this.props.currMenuIndices[menuName]}
          >
            {renderFunc(item)}
          </MenuItem>
        ))}
      </MenuList>
    );
  };

  renderListedAccount = account => {
    return account["Type"] + ":" + account["Address"] + "...";
  };

  renderListedContract = contract => {
    return contract.name + ":" + contract.address;
  };

  renderListedRole = role => {
    return role;
  };

  renderSelectedContract = () => {
    const index = this.props.currMenuIndices["contract"];
    let val = this.props.allContracts[index];
    return "Contract: " + val.name + " (" + shortAddr(val.address) + "...)";
  };

  renderSelectedRole = () => {
    const index = this.props.currMenuIndices["role"];
    let val = this.props.allRoles[index];
    return "Role: " + val;
  };

  renderSelectedAccount = () => {
    let val =
      this.props.allAccounts === undefined
        ? [{ Type: "None", Address: "None" }]
        : this.props.allAccounts;
    const index = this.props.currMenuIndices["account"];
    return (
      "Account: " +
      val[index]["Type"] +
      " (" +
      shortAddr(val[index]["Address"]) +
      "...)"
    );
  };

  handleMenuItemClick = (menuName, index) => () => {
    // TODO: is there a better way to pass props up if I want them at each level?
    this.props.setCurrMenuIndex(menuName)(index);
    this.handleClose();
  };

  handleMenu = menuName => event => {
    this.setState({ menuOpen: menuName, anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null, menuOpen: null });
  };

  render() {
    const { anchorEl } = this.state;

    // const displayAccounts = (selected)
    const contracts = this.props.allContracts;

    const { allAccounts } = this.props;
    let accounts =
      allAccounts !== undefined
        ? allAccounts
        : [{ Type: "None", Address: "None" }];

    const roles = this.props.allRoles;

    return (
      <Fragment>
        <CustomMenuItem
          anchorEl={this.state.anchorEl}
          menuOpen={this.state.menuOpen}
          menuName={"menu-role"}
          handleMenu={this.handleMenu}
          renderSelected={this.renderSelectedRole}
          handleClose={this.handleClose}
          currAccountIndex={this.props.currMenuIndices["role"]}
          renderMenuList={this.renderMenuList(
            "role",
            roles,
            this.renderListedRole
          )}
        />
        <CustomMenuItem
          anchorEl={this.state.anchorEl}
          menuOpen={this.state.menuOpen}
          menuName={"menu-contract"}
          handleMenu={this.handleMenu}
          renderSelected={this.renderSelectedContract}
          handleClose={this.handleClose}
          currAccountIndex={this.props.currMenuIndices["contract"]}
          renderMenuList={this.renderMenuList(
            "contract",
            contracts,
            this.renderListedContract
          )}
        />
        <CustomMenuItem
          anchorEl={this.state.anchorEl}
          menuOpen={this.state.menuOpen}
          menuName={"menu-account"}
          handleMenu={this.handleMenu}
          renderSelected={this.renderSelectedAccount}
          handleClose={this.handleClose}
          currAccountIndex={this.props.currMenuIndices["account"]}
          renderMenuList={this.renderMenuList(
            "account",
            accounts,
            this.renderListedAccount
          )}
        />
        <Menu
          id="menu-role"
          anchorEl={anchorEl}
          anchorOrigin={{
            vertical: "top",
            horizontal: "right"
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "right"
          }}
          open={this.state.menuOpen === "role"}
          onClose={this.handleClose}
        >
          <MenuItem onClick={this.handleClose}>Testator</MenuItem>
          <MenuItem onClick={this.handleClose}>Beneficiary</MenuItem>
        </Menu>
      </Fragment>
    );
  }
}

CustomMenu.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(CustomMenu);
