import React, { Fragment } from "react";
import PropTypes from "prop-types";
import Button from "@material-ui/core/Button";
import { withStyles } from "@material-ui/core/styles";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import InfoIcon from "@material-ui/icons/Info";
import Tooltip from "@material-ui/core/Tooltip";
import TextField from "@material-ui/core/TextField";
import Switch from "@material-ui/core/Switch";
import FormControlLabel from "@material-ui/core/FormControlLabel";

const styles = theme => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit
  },
  dense: {
    marginTop: 16
  },
  menu: {
    width: 200
  },
  button: {
    margin: theme.spacing.unit
  }
});

class TestatorSecretView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false
    };
    //this.handleCloseEdit = this.handleCloseEdit.bind(this);
  }

  handleClickOpen = () => {
    this.setState({ open: true });
  };

  render() {
    const longText = `
                  The secret, which should already be encrypted for the beneficiary with PGP.
              `;

    const { classes } = this.props;

    const actionType = this.props.actionType;
    const shouldDisable = actionType === "View" || this.props.secretViewLoading;

    let secret = {};

    if (actionType === "View") {
      secret = this.props.secretProp;
    } else if (actionType === "Edit" || actionType === "New") {
      secret = this.props.editableSecret;
    }

    const secretUndefinedView = (
      <div style={{ padding: 20 }}>
        <strong>
          No secret selected. Please select an existing secret or add a new one.
        </strong>
      </div>
    );

    if ((secret === undefined || secret === {}) && actionType !== "New") {
      return secretUndefinedView;
    }

    const blankName = secret["name"] === undefined || secret["name"] === "";

    // Necessary because TextField doesn't update if value is undefined.
    const toStr = key => {
      return key === undefined ? "" : key;
    };

    // Necessary because TextField doesn't update if value is undefined.
    const toStrOrZero = key => {
      return key === undefined ? "0" : key;
    };

    return (
      <div>
        <div style={{ padding: 10, fontSize: "150%" }}>
          {actionType + " Secret"}
        </div>
        <Divider />
        <div>
          <div>
            <strong>{"Secret name: "}</strong>
            <Tooltip
              title={
                "Currently stored unencrypted for both testator and beneficiary."
              }
            >
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
            <div>
              <TextField
                id="outlined-multiline-static"
                className={classes.textField}
                disabled={shouldDisable}
                onChange={this.props.updateEditable("name")}
                value={toStr(secret["name"])}
                margin="normal"
                variant="outlined"
                helperText={blankName ? "Error: Name cannot be blank" : ""}
                error={blankName}
              />
            </div>
          </div>
          <br />
          <div>
            <strong>{"Notes for beneficiary: "}</strong>
            <Tooltip
              title={
                "Will only be viewable by the beneficiary and the testator."
              }
            >
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
          </div>
          <div>
            <TextField
              id="outlined-multiline-static"
              multiline
              rows="4"
              onChange={this.props.updateEditable("beneficiary_notes")}
              value={toStr(secret["beneficiary_notes"])}
              className={classes.textField}
              disabled={shouldDisable}
              margin="normal"
              variant="outlined"
            />
          </div>
          <div>
            <strong>{"Time Delay: "}</strong>
            <Tooltip
              title={
                "A fixed amount of time from the last check in, before the secret can be viewed by the beneficiary."
              }
            >
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
          </div>
          <div>
            {["days", "hours", "minutes", "seconds"].map(input => {
              return (
                <Fragment>
                  {input}
                  <TextField
                    id="outlined-multiline-static"
                    value={toStrOrZero(secret.timeBeforeReveal[input])}
                    onChange={this.props.updateTimeEditable(
                      "timeBeforeReveal",
                      input
                    )}
                    className={classes.textField}
                    disabled={shouldDisable}
                    margin="normal"
                    variant="outlined"
                    style={{ width: 50 }}
                    inputProps={{
                      maxLength: 2
                    }}
                  />
                </Fragment>
              );
            })}
          </div>
          <div>
            <Tooltip title={longText}>
              <strong>{"Secret: "}</strong>
            </Tooltip>

            <FormControlLabel
              style={{ paddingLeft: 10 }}
              control={
                <Switch
                  checked={this.state.checkedB}
                  onChange={this.props.closeEdit}
                  value="checkedB"
                  color="primary"
                />
              }
              label={
                <Tooltip
                  title={
                    "Will show the secret without Enigma's encryption (not recommended)"
                  }
                >
                  <IconButton aria-label="Info">
                    <InfoIcon />
                  </IconButton>
                </Tooltip>
              }
            />
          </div>
          <div style={{ padding: 10 }}>
            <TextField
              id="outlined-multiline-static"
              multiline
              rows="4"
              value={toStr(secret.secret)}
              onChange={this.props.updateEditable("secret")}
              className={classes.textField}
              disabled={shouldDisable}
              margin="normal"
              variant="outlined"
            />
          </div>
          <div>
            <strong>{"Beneficiary ETH address: "}</strong>
            <Tooltip
              title={
                "Will be used to identify the beneficiary, make sure this is correct."
              }
            >
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
            <div>
              <TextField
                id="outlined-multiline-static"
                value={toStr(secret.beneficiary_addr)}
                onChange={this.props.updateEditable("beneficiary_addr")}
                className={classes.textField}
                disabled={shouldDisable}
                margin="normal"
                variant="outlined"
              />
            </div>
          </div>
          <Divider />
        </div>
        {actionType !== "View" ? (
          <div>
            <Button
              onClick={this.props.handleSave}
              className={classes.button}
              color="primary"
              variant={"contained"}
              disabled={blankName || this.props.secretViewLoading}
            >
              Save
            </Button>
            <Button
              className={classes.button}
              onClick={this.props.handleCancel}
              color="primary"
              variant={"contained"}
              disabled={this.props.secretViewLoading}
            >
              Cancel
            </Button>
          </div>
        ) : (
          <div />
        )}
      </div>
    );
  }
}

TestatorSecretView.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(TestatorSecretView);
