import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import SecretList from "./SecretList";
import TestatorSecretView from "./TestatorSecretView";
import { HashRouter as Router } from "react-router-dom";
import CustomAppBar from "./CustomAppBar";
import RefreshIcon from "@material-ui/icons/Refresh";
import Web3 from "web3";
import IconButton from "@material-ui/core/IconButton/IconButton";
import ReactGA from "react-ga";
import CircularProgress from "@material-ui/core/CircularProgress";

import secretKeeperContractDefinition from "./contracts/SecretKeeper.json";
import { MuiThemeProvider } from "@material-ui/core/styles";
import { createMuiTheme } from "@material-ui/core/styles";
import createPalette from "@material-ui/core/styles/createPalette";
import CssBaseline from "@material-ui/core/es/CssBaseline/CssBaseline";
import AppSetup from "./AppSetup";
import BeneficiarySecretView from "./BeneficiarySecretView";
import BeneficiarySecretList from "./BeneficiarySecretList";

var contract = require("truffle-contract");

const GAS = "400000";

const theme = createMuiTheme({
  palette: createPalette({
    primary: {
      light: "#757ce8",
      main: "#3c9eb5",
      dark: "#506184",
      contrastText: "#fff"
    },
    secondary: {
      light: "#ff7961",
      main: "#f44336",
      dark: "#ba000d",
      contrastText: "#000"
    }
  })
});

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  grow: {
    flexGrow: 1
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20
  },
  button: {
    margin: theme.spacing.unit * 2
  },
  progress: {
    margin: theme.spacing.unit * 2
  }
});

const staticContracts = [
  {
    name: "version_1.0",
    address: secretKeeperContractDefinition.networks["3"].address
  }
];

class MainApp extends React.Component {
  constructor(props) {
    super(props);
    this.isUnlocked = this.isUnlocked.bind(this);
    this.setAccountsIfMetamaskUnlocked = this.setAccountsIfMetamaskUnlocked.bind(
      this
    );
  }

  isMetamaskInstalled() {
    if (typeof web3 !== "undefined" && window.web3.currentProvider.isMetaMask) {
      console.log("METAMASK INSTALLED!");
      return true;
    } else {
      console.log("NOT INSTALLED");
      return false;
    }
  }

  // TODO: fix bug in original where getNetwork() isn't a function:
  // https://github.com/MetaMask/faq/blob/master/DEVELOPERS.md
  // See https://github.com/ethereum/web3.js/issues/1353
  // Also, apparently this isn't a completely accurate method:
  // https://web3js.readthedocs.io/en/1.0/web3-eth-net.html#getnetworktype
  async logNetworkModern() {
    window.web3.eth.net.getNetworkType((err, network) => {
      console.log("Using network: " + network);
      this.setState({ network: network });
    });
  }

  // https://timestamp.online/article/how-to-convert-timestamp-to-datetime-in-javascript
  async getCurrTimestamp() {
    let blockNumber = await window.web3.eth.getBlockNumber();
    let currBlock = await window.web3.eth.getBlock(blockNumber);
    // TODO: throw a better error if currBlock.timestamp doesn't exist, currBlock can be null!
    if (currBlock === null || currBlock.timestamp === undefined) {
      console.log("couldn't get current block timestamp");
      return;
    }
    let ts = new Date(currBlock.timestamp * 1000);
    this.setState({
      currTimestamp: ts.toLocaleString(),
      currTs: currBlock.timestamp
    });
  }

  async doCheckIn() {
    let account = this.state.allAccounts[0];
    this.setState({ checkInLoading: true, checkInErrMsg: "Checking in..." });
    this.state.secretKeeperContract
      .checkIn({ from: account.Address, gas: GAS })
      .then(result => {
        console.log("Got check in receipt: " + JSON.stringify(result));
        this.state.secretKeeperContract
          .getLastCheckIn({ from: account.Address, gas: GAS })
          .then(result => {
            console.log("Got last check in result: " + JSON.stringify(result));
            // TODO: error check here, of course.
            let ts = new Date(parseInt(result) * 1000);
            this.setState({
              lastCheckIn: ts.toLocaleString(),
              checkInLoading: false,
              checkInErrMsg: "Check in complete!"
            });
          })
          .catch(err => {
            console.log("Error when getting new last check in: " + err.message);
            this.setState({
              checkInLoading: false,
              checkInErrMsg: err.message
            });
          });
      })
      .catch(err => {
        console.log("Error when checking in: " + err.message);
        this.setState({
          checkInLoading: false,
          checkInErrMsg: err.message
        });
      });
  }

  setAccountsIfMetamaskUnlocked = (err, accounts) => {
    if (err != null) {
      console.log(err);
      return false;
    } else if (accounts.length === 0) {
      console.log("MetaMask is locked");
      return false;
    } else {
      console.log("MetaMask is unlocked: " + accounts);
      let allAccounts = [];
      for (var i = 0; i < accounts.length; i++) {
        allAccounts.push({ Type: "Metamask", Address: accounts[i] });
      }
      this.setState({
        metamaskUnlocked: true,
        allAccounts: allAccounts
      });

      this.registerInstance();
      return true;
    }
  };

  async registerInstance() {
    // https://github.com/trufflesuite/truffle-contract#full-example
    var contractAddr2 = secretKeeperContractDefinition.networks["3"].address;
    console.log("Using secret keeper contract address: " + contractAddr2);
    var instance2;
    var SecretKeeper = contract(secretKeeperContractDefinition);
    SecretKeeper.setProvider(window.web3.currentProvider);

    // add snackbar for "please complete metamask transaction"?
    // Could use deployed() as well.
    // Get contract instance and last check in time.

    SecretKeeper.at(contractAddr2)
      .then(result => {
        instance2 = result;

        console.log("Instance addr: " + instance2.address);
        this.setState({ secretKeeperContract: instance2 });
        this.getSecretsForTestator(instance2);
        return instance2.getLastCheckIn({
          from: this.state.allAccounts[0].Address,
          gas: GAS
        });
      })
      .then(result => {
        console.log("Result for old last check in: " + JSON.stringify(result));
        let intTs = parseInt(result);
        let ts = new Date(intTs * 1000);
        this.setState({
          lastCheckIn: intTs === 0 ? "None" : ts.toLocaleString()
        });
      })
      .catch(err => {
        console.log(
          "Error for getting initial secret and last check in (testator): " +
            JSON.stringify(err)
        );
        this.setState({ checkInErrMsg: err.message });
      });
  }

  async getSecretsForBeneficiary(instance) {
    const account = this.state.allAccounts[0];

    instance
      .getSecretsForBeneficiary({
        from: account.Address,
        gas: GAS
      })
      .then(result => {
        console.log(
          "Result for secrets (beneficiary): " + JSON.stringify(result)
        );
        var staticSecrets = [];
        var secrets = result.secrets;
        var names = result.names;
        var timeBeforeReveals = result.timeBeforeReveals;
        var lastCheckIns = result.lastCheckIns;
        var owners = result.owners;
        var beneficiaryNotes = result.beneficiaryNotes;

        for (var i = 0; i < secrets.length; i++) {
          staticSecrets.push({
            name: names[i],
            testator_notes: "NONE",
            beneficiary_notes: beneficiaryNotes[i],
            timeBeforeReveal: this.convertMS(timeBeforeReveals[i]),
            lastCheckIn: lastCheckIns[i],
            secret: window.web3.utils.hexToAscii(secrets[i]),
            owner_addr: owners[i]
          });
        }
        // TODO: maybe use different allSecrets?
        this.setState({
          allSecrets: staticSecrets,
          // TODO: this is a little hacky and hidden
          currSecretIndex: undefined
        });
      })
      .catch(err => {
        console.log(
          "Error for getting secrets (beneficiary): " + JSON.stringify(err)
        );
        this.setState({ checkInErrMsg: err.message });
      });
  }

  async getSecretsForTestator(contractInstance) {
    const account = this.state.allAccounts[0];

    contractInstance
      .getSecretsForTestator({
        from: account.Address,
        gas: GAS
      })
      .then(result => {
        console.log("Result for secrets (testator): " + JSON.stringify(result));
        var staticSecrets = [];
        var names = result.names;
        var secrets = result.secrets;
        var times = result.times;
        var beneficiaries = result.beneficiaries;
        var beneficiaryNotes = result.beneficiaryNotes;

        for (var i = 0; i < secrets.length; i++) {
          staticSecrets.push({
            name: names[i],
            testator_notes: "NONE",
            beneficiary_notes: beneficiaryNotes[i],
            timeBeforeReveal: this.convertMS(times[i]),
            secret: window.web3.utils.hexToAscii(secrets[i]),
            beneficiary_addr: beneficiaries[i]
          });
        }
        this.setState({
          allSecrets: staticSecrets,
          // TODO: this is a little hacky and hidden
          currSecretIndex: undefined
        });
      })
      .catch(err => {
        console.log(
          "Error for getting secrets (testator): " + JSON.stringify(err)
        );
        this.setState({ checkInErrMsg: err.message });
      });
  }

  convertMS(ms) {
    var d, h, m, s;
    s = Math.floor(ms / 1000);
    m = Math.floor(s / 60);
    s = s % 60;
    h = Math.floor(m / 60);
    m = m % 60;
    d = Math.floor(h / 24);
    h = h % 24;
    return { days: d, hours: h, minutes: m, seconds: s };
  }

  convertJSONtoMS(obj) {
    var wrap = obj => {
      return obj === undefined ? 0 : obj;
    };
    var ms =
      1000 * parseInt(wrap(obj.seconds)) +
      60 * 1000 * parseInt(wrap(obj.minutes)) +
      60 * 60 * 1000 * parseInt(wrap(obj.hours)) +
      24 * 60 * 60 * 1000 * parseInt(wrap(obj.days));
    return ms;
  }

  // Should ONLY be called if installed is true.
  isUnlocked() {
    window.web3.eth.getAccounts(this.setAccountsIfMetamaskUnlocked);
  }

  state = {
    anchorEl: null,
    menuOpen: null,
    mmState: this.mmState,
    allAccounts: undefined,
    allContracts: staticContracts,
    currMenuIndices: { account: 0, contract: 0, role: 0 },
    currSecretIndex: undefined,
    panelActionType: "View",
    editableSecret: {},
    //allSecrets: staticSecrets,
    allSecrets: [],
    allRoles: ["Testator", "Beneficiary"],
    currTimestamp: "None",
    currTs: 0,
    lastCheckIn: "None",
    secretKeeperContract: undefined,
    checkInErrMsg: "",
    checkInLoading: false,
    secretViewErrMsg: "",
    secretViewLoading: false,
    decryptedSecret: undefined,
    // App setup state
    metamaskInstalled: false,
    metamaskUnlocked: false,
    disclaimerChecked: false,
    network: "None",
    shouldLaunch: false
  };

  isSetupComplete = () => {
    return (
      this.state.metamaskInstalled &&
      this.state.metamaskUnlocked &&
      this.state.network === "ropsten" &&
      this.state.disclaimerChecked &&
      this.state.shouldLaunch
    );
  };

  handleLaunch = () => {
    this.setState({ shouldLaunch: true });
  };

  setCurrMenuIndex = menuName => index => {
    let newInds = JSON.parse(JSON.stringify(this.state.currMenuIndices));
    newInds[menuName] = index;

    // Update the state for the selection.
    if (this.state.currMenuIndices.role !== newInds.role) {
      if (newInds.role === 0) {
        // TODO: get secrets for beneficiary.
        this.getSecretsForTestator(this.state.secretKeeperContract);
      } else if (newInds.role === 1) {
        this.getSecretsForBeneficiary(this.state.secretKeeperContract);
      }
    }

    this.setState({ currMenuIndices: newInds });
  };

  componentDidMount() {
    this.metamaskSetup();
  }

  metamaskSetup = () => {
    // TODO: probably don't want to attach this to the window every time.
    (async () => {
      this.setState({ mmState: "NOT_LOGGED_IN (please complete login)" });
      var metamaskInstalled = false;
      // Modern dapp browsers...
      if (window.ethereum) {
        window.web3 = new Web3(window.ethereum);
        metamaskInstalled = this.isMetamaskInstalled();
        // Do an update now since we need to request account access.
        this.setState({ metamaskInstalled: metamaskInstalled });
        try {
          // Request account access if needed
          // TODO: if the user just exits here, it doesn't throw an error.
          await window.ethereum.enable();
          this.mmState = "Connected";

          this.logNetworkModern();
          this.getCurrTimestamp();

          // Acccounts now exposed
          /*
          window.web3.eth.sendTransaction({

          });
          */
        } catch (error) {
          // User denied account access...
          console.log(error);
          this.mmState = "USER ACCESS DENIED: Got an error: " + error;
        }
      }
      // Legacy dapp browsers...
      else if (window.web3) {
        window.web3 = new Web3(window.web3.currentProvider);
        metamaskInstalled = this.isMetamaskInstalled();
        // Do an update now since we need to request account access.
        this.setState({ metamaskInstalled: metamaskInstalled });
        // Acccounts always exposed
        this.mmState = "OK: Found legacy Dapp browser.";
        // TODO: test if you need to log in a different manner here with web3.version.getNetwork();
        /*
        window.web3.eth.sendTransaction({

        });
        */
        this.logNetworkModern();
        this.getCurrTimestamp();
      }
      // Non-dapp browsers...
      else {
        this.mmState = "NO_SUPPORT";
        let msg =
          "Non-Ethereum browser detected. You should consider trying MetaMask!";
        console.log(msg);
        this.mmState = "NO_SUPPORT: " + msg;
      }

      if (metamaskInstalled) {
        // TODO: this does a setState() for allAccounts., make it more clear.
        await this.isUnlocked();
      }

      this.setState({
        mmState: this.mmState,
        metamaskInstalled: metamaskInstalled
      });
    })();
  };

  handleChange = event => {
    this.setState({ auth: event.target.checked });
  };

  handleMenu = menuName => event => {
    this.setState({ menuOpen: menuName });
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ menuOpen: null, anchorEl: null });
  };

  handleViewOnClick = index => () => {
    this.setState({ currSecretIndex: index, panelActionType: "View" });
  };

  handleEditClick = index => (event, value) => {
    let toEdit = this.state.allSecrets[index];

    this.setState({
      currSecretIndex: index,
      panelActionType: "Edit",
      editableSecret: JSON.parse(JSON.stringify(toEdit))
    });
  };

  handleReloadSetup = () => {
    this.metamaskSetup();
  };

  handleCancel = event => {
    this.setState({ currSecretIndex: undefined, panelActionType: "View" });
  };

  handleNewSecret = event => {
    this.setState({
      currSecretIndex: undefined,
      panelActionType: "New",
      editableSecret: { timeBeforeReveal: {} }
    });
  };

  handleSave = async event => {
    if (this.state.panelActionType === "Edit") {
      this.editSecret();
    } else if (this.state.panelActionType === "New") {
      this.addNewSecret();
    }
  };

  editSecret() {
    this.setState({
      secretViewLoading: true,
      secretViewErrMsg: "Editing secret..."
    });
    // TODO: decide how to input timestamp.
    let newSecret = this.state.editableSecret;

    var name = newSecret.name;
    var beneficiaryNotes = newSecret.beneficiary_notes;
    var timeDelayMs = this.convertJSONtoMS(newSecret.timeBeforeReveal);
    var secret = window.web3.utils.asciiToHex(newSecret.secret);
    var beneficiaryAddr = newSecret.beneficiary_addr;

    const account = this.state.allAccounts[0];

    this.state.secretKeeperContract
      .editTestatorSecretInfo(
        this.state.currSecretIndex,
        secret,
        name,
        timeDelayMs,
        beneficiaryAddr,
        beneficiaryNotes,
        { from: account.Address, gas: GAS }
      )
      .then(result => {
        console.log(result);
        this.setState({
          secretViewErrMsg: "Edit success!",
          secretViewLoading: false,
          panelActionType: "View",
          currSecretIndex: undefined
        });
        this.getSecretsForTestator(this.state.secretKeeperContract);
      })
      .catch(err => {
        console.log(err.message);
        this.setState({
          secretViewErrMsg: err.message,
          secretViewLoading: false
        });
      });
  }

  addNewSecret() {
    this.setState({
      secretViewLoading: true,
      secretViewErrMsg: "Adding secret..."
    });
    // TODO: decide how to input timestamp.
    let newSecret = this.state.editableSecret;

    var name = newSecret.name;
    var beneficiaryNotes = newSecret.beneficiary_notes;
    var timeDelayMs = this.convertJSONtoMS(newSecret.timeBeforeReveal);
    var secret = window.web3.utils.asciiToHex(newSecret.secret);
    var beneficiaryAddr = newSecret.beneficiary_addr;

    const account = this.state.allAccounts[0];

    this.state.secretKeeperContract
      .addTestatorSecretInfo(
        secret,
        name,
        timeDelayMs,
        beneficiaryAddr,
        beneficiaryNotes,
        { from: account.Address, gas: GAS }
      )
      .then(result => {
        console.log(result);
        this.setState({
          secretViewErrMsg: "Success!",
          secretViewLoading: false,
          panelActionType: "View",
          currSecretIndex: undefined
        });

        this.getSecretsForTestator(this.state.secretKeeperContract);
      })
      .catch(err => {
        console.log(err.message);
        this.setState({
          secretViewErrMsg: err.message,
          secretViewLoading: false
        });
      });
  }

  updateEditable = key => event => {
    let editedSecret = this.state.editableSecret;
    editedSecret[key] = event.target.value;
    this.setState({ editableSecret: editedSecret });
  };

  updateTimeEditable = (key, timeKey) => event => {
    let editedSecret = this.state.editableSecret;
    editedSecret[key][timeKey] = event.target.value;
    this.setState({ editableSecret: editedSecret });
  };

  // Calls getSecretsForTestator() to update secret list after successful transaction receipt.
  handleDelete = index => event => {
    this.setState({
      secretViewErrMsg: "Deleting secret...",
      secretViewLoading: true
    });

    const account = this.state.allAccounts[0];

    this.state.secretKeeperContract
      .deleteTestatorSecretInfoByIndex(index, {
        from: account.Address,
        gas: GAS
      })
      .then(result => {
        console.log(result);
        this.setState({
          secretViewErrMsg: "Delete success!",
          secretViewLoading: false
        });
        this.getSecretsForTestator(this.state.secretKeeperContract);
      })
      .catch(err => {
        console.log(err.message);
        this.setState({
          secretViewErrMsg: err.message,
          secretViewLoading: false
        });
      });
  };

  handleRefresh = event => {
    this.getCurrTimestamp();
    this.getSecretsForTestator(this.state.secretKeeperContract);
  };

  handleCheckIn = async event => {
    ReactGA.event({
      category: "User",
      action: "Check In"
    });
    this.doCheckIn();
  };

  handleCheckDisclaimer = event => {
    this.setState({ disclaimerChecked: event.target.checked });
  };

  handleDecryptSecret = index => () => {
    // TODO: make separate views for each action?
    this.setState({
      secretViewErrMsg: "Mock decrypting secret...",
      secretViewLoading: true
    });

    const account = this.state.allAccounts[0];

    this.state.secretKeeperContract
      .decryptSecretForBeneficiary(index, {
        from: account.Address,
        gas: GAS
      })
      .then(result => {
        this.setState({
          secretViewErrMsg: "Mock decrypt success!",
          secretViewLoading: false,
          decryptedSecret: window.web3.utils.hexToAscii(result)
        });
      })
      .catch(err => {
        console.log(err.message);
        this.setState({
          secretViewErrMsg: err.message,
          secretViewLoading: false
        });
      });
  };

  render() {
    const { classes } = this.props;

    // TODO: find a better way to handle this case in all components.
    const allAccounts =
      this.state.allAccounts === undefined
        ? [{ Type: "NONE", Address: "None" }]
        : this.state.allAccounts;

    const secretProp =
      this.state.currSecretIndex === undefined
        ? undefined
        : JSON.parse(
            JSON.stringify(this.state.allSecrets[this.state.currSecretIndex])
          );

    // TODO: this is a hack and needs to be cleaned up.
    if (!this.isSetupComplete()) {
      return (
        <MuiThemeProvider theme={theme}>
          <div className={classes.root}>
            <CustomAppBar
              allAccounts={this.state.allAccounts}
              allContracts={this.state.allContracts}
              allRoles={this.state.allRoles}
              currMenuIndices={this.state.currMenuIndices}
              setCurrMenuIndex={this.setCurrMenuIndex}
              setupComplete={false}
            />
            <AppSetup
              metamaskInstalled={this.state.metamaskInstalled}
              metamaskUnlocked={this.state.metamaskUnlocked}
              network={this.state.network}
              disclaimerChecked={this.state.disclaimerChecked}
              handleCheckDisclaimer={this.handleCheckDisclaimer}
              handleLaunch={this.handleLaunch}
              handleReload={this.handleReloadSetup}
            />
          </div>
        </MuiThemeProvider>
      );
    }

    const renderSecretPanel = () => {
      if (
        (this.state.secretViewErrMsg !== "" &&
          this.state.secretViewErrMsg !== undefined) ||
        this.state.secretViewLoading
      ) {
        return (
          <Grid item>
            <Paper align="center">
              <div style={{ padding: 10 }}>
                {"Message: " + this.state.secretViewErrMsg}
                {this.state.secretViewLoading ? (
                  <div>
                    <CircularProgress className={classes.progress} />
                  </div>
                ) : (
                  ""
                )}
              </div>
            </Paper>
          </Grid>
        );
      }
    };

    const renderCheckInPanel = () => {
      if (
        (this.state.checkInErrMsg !== "" &&
          this.state.checkInErrMsg !== undefined) ||
        this.state.checkInLoading
      ) {
        return (
          <Grid item>
            <Paper align="center">
              <div style={{ padding: 10 }}>
                {this.state.checkInErrMsg}
                {this.state.checkInLoading ? (
                  <div>
                    <CircularProgress className={classes.progress} />
                  </div>
                ) : (
                  ""
                )}
              </div>
            </Paper>
          </Grid>
        );
      }
    };

    return (
      <MuiThemeProvider theme={theme}>
        <div className={classes.root}>
          <CustomAppBar
            allAccounts={this.state.allAccounts}
            allContracts={this.state.allContracts}
            allRoles={this.state.allRoles}
            currMenuIndices={this.state.currMenuIndices}
            setCurrMenuIndex={this.setCurrMenuIndex}
            setupComplete={true}
          />

          <Grid
            container
            spacing={8}
            direction="row"
            alignItems="stretch"
            alignContent="center"
            justify="center"
            style={{ paddingTop: "10px" }}
          >
            {/* TODO: clean up the mmstate and figure out what to display. */}
            <Grid item xs={10}>
              {/* Outer grid*/}
              {/*Left and right panes */}
              <Grid container direction="row" spacing={8}>
                {/* Left pane */}
                <Grid item container direction="column" spacing={8} xs={4}>
                  <Grid item>
                    <Paper align="center">
                      {this.state.currMenuIndices.role === 0 ? (
                        <SecretList
                          currAccount={
                            allAccounts[this.state.currMenuIndices["account"]]
                          }
                          handleEditClick={this.handleEditClick}
                          secrets={this.state.allSecrets}
                          handleViewOnClick={this.handleViewOnClick}
                          currSecretIndex={this.state.currSecretIndex}
                          handleCancel={this.handleCancel}
                          handleNewSecret={this.handleNewSecret}
                          panelActionType={this.state.panelActionType}
                          handleDelete={this.handleDelete}
                          secretKeeperContract={this.state.secretKeeperContract}
                          role={this.state.currMenuIndices.role}
                        />
                      ) : (
                        <BeneficiarySecretList
                          secrets={this.state.allSecrets}
                          currSecretIndex={this.state.currSecretIndex}
                          handleViewOnClick={this.handleViewOnClick}
                          panelActionType={this.state.panelActionType}
                          secretKeeperContract={this.state.secretKeeperContract}
                          role={this.state.currMenuIndices.role}
                          currAccount={
                            allAccounts[this.state.currMenuIndices["account"]]
                          }
                        />
                      )}
                    </Paper>
                  </Grid>
                  <Grid item>
                    <Paper align="center">
                      <div style={{ padding: 10 }}>
                        <strong>Last check in:</strong>
                        {this.state.lastCheckIn}
                      </div>
                      <div style={{ padding: 10 }}>
                        <strong>Current timestamp:</strong>
                        {this.state.currTimestamp}
                      </div>
                      <div>
                        {/*TODO: assuming accounts are loaded here, but you should actually check in case someone logged out.*/}
                        <Button
                          color="primary"
                          variant="contained"
                          className={classes.button}
                          disabled={
                            this.state.secretKeeperContract === undefined
                          }
                          onClick={this.handleCheckIn}
                        >
                          Check In
                        </Button>
                        <IconButton
                          aria-label="Refresh"
                          disabled={
                            this.state.secretKeeperContract === undefined
                          }
                        >
                          <RefreshIcon onClick={this.handleRefresh} />
                        </IconButton>
                      </div>
                    </Paper>
                  </Grid>
                  {renderCheckInPanel()}
                  {renderSecretPanel()}
                </Grid>
                {/* Right pane */}
                <Grid item xs={8}>
                  <div className={classes.root}>
                    <Paper align={"center"}>
                      {this.state.currMenuIndices.role === 0 ? (
                        <TestatorSecretView
                          secretProp={secretProp}
                          actionType={this.state.panelActionType}
                          editableSecret={this.state.editableSecret}
                          updateEditable={this.updateEditable}
                          updateTimeEditable={this.updateTimeEditable}
                          handleSave={this.handleSave}
                          handleCancel={this.handleCancel}
                          secretViewLoading={this.state.secretViewLoading}
                        />
                      ) : (
                        <BeneficiarySecretView
                          secretProp={secretProp}
                          actionType={this.state.panelActionType}
                          handleRetrieveSecret={this.handleRetrieveSecret}
                          secretViewLoading={this.state.secretViewLoading}
                          currTs={this.state.currTs}
                          convertJSONToMS={this.convertJSONtoMS}
                          decryptedSecret={this.state.decryptedSecret}
                          decryptSecret={this.handleDecryptSecret(
                            this.state.currSecretIndex
                          )}
                        />
                      )}
                    </Paper>
                  </div>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </div>
      </MuiThemeProvider>
    );
  }
}

MainApp.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(MainApp);
