import React, { Fragment } from "react";
import PropTypes from "prop-types";
import Button from "@material-ui/core/Button";
import { withStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Slide from "@material-ui/core/Slide";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import ListItemText from "@material-ui/core/ListItemText";
import IconButton from "@material-ui/core/IconButton";
import DeleteIcon from "@material-ui/icons/Delete";
import EditIcon from "@material-ui/icons/Edit";

const styles = theme => ({
  button: {
    margin: theme.spacing.unit * 2
  },
  root: {
    width: "100%",
    maxWidth: 540,
    backgroundColor: theme.palette.background.paper
  },
  toolbarButtons: {
    marginLeft: "auto"
  },
  customWidth: {
    maxWidth: 500
  },
  noMaxWidth: {
    maxWidth: "none"
  }
});

function Transition(props) {
  return <Slide direction="up" {...props} />;
}

class SecretList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      openDeleteDialog: false,
      currSecretName: undefined,
      indexToDelete: undefined,
      openEdit: false
    };
  }

  handleCloseEdit = () => {
    this.setState({ openEdit: false });
  };

  handleOpenEdit = () => {
    this.setState({ openEdit: true });
  };

  handleCloseDelete = event => {
    this.setState({ openDeleteDialog: false, indexToDelete: undefined });
  };

  handleOpenDelete = index => event => {
    this.setState({
      openDeleteDialog: true,
      currSecretName: this.props.secrets[index]["name"],
      indexToDelete: index
    });
  };

  closeAndDelete = index => event => {
    this.setState({ indexToDelete: undefined, openDeleteDialog: false });
    this.props.handleDelete(index)(null);
  };

  render() {
    const { classes } = this.props;
    const secrets = this.props.secrets;
    let currAccount =
      this.props.currAccount === undefined
        ? { Type: "None", Address: "None" }
        : this.props.currAccount;
    const secretName =
      this.state.indexToDelete === undefined
        ? "NONE"
        : secrets[this.state.indexToDelete].name;
    return (
      <div className={classes.root}>
        {"Testator Address: " + currAccount["Address"].substring(0, 10) + "..."}
        <br />
        {"Secrets (" + secrets.length + ")"}
        <List>
          {secrets.map((value, index) => {
            return (
              <ListItem
                key={index}
                role={undefined}
                dense
                button
                selected={this.props.currSecretIndex === index}
                onClick={this.props.handleViewOnClick(index)}
              >
                <ListItemText primary={value["name"]} />
                <ListItemSecondaryAction>
                  <IconButton
                    aria-label="Edit"
                    onClick={this.props.handleEditClick(index)}
                  >
                    <EditIcon />
                  </IconButton>
                  <IconButton aria-label="Delete">
                    <DeleteIcon onClick={this.handleOpenDelete(index)} />
                  </IconButton>
                </ListItemSecondaryAction>
              </ListItem>
            );
          })}
        </List>
        <Button
          onClick={
            this.props.panelActionType === "New"
              ? this.props.handleCancel
              : this.props.handleNewSecret
          }
          color={this.props.panelActionType === "New" ? "secondary" : "primary"}
          variant={"contained"}
          disabled={
            this.props.panelActionType === "New" ||
            this.props.secretKeeperContract === undefined
          }
          className={classes.button}
        >
          {"New Secret"}
        </Button>
        <DeleteDialog
          openDeleteDialog={this.state.openDeleteDialog}
          handleCloseDelete={this.handleCloseDelete}
          closeAndDelete={this.closeAndDelete}
          indexToDelete={this.state.indexToDelete}
          secretName={secretName}
        />
      </div>
    );
  }
}

SecretList.propTypes = {
  classes: PropTypes.object.isRequired
};

class DeleteDialog extends React.Component {
  render() {
    return (
      <Fragment>
        <Dialog
          open={this.props.openDeleteDialog}
          TransitionComponent={Transition}
          keepMounted
          onClose={this.props.handleCloseDelete}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle id="alert-dialog-slide-title">
            {"Confirm Delete"}
          </DialogTitle>
          <Divider />
          <DialogContent>
            <div>
              <div>
                <strong>
                  {'Are you sure you want to delete the secret "' +
                    this.props.secretName +
                    '"?'}
                </strong>
              </div>
            </div>
            <DialogContentText id="alert-dialog-slide-description" />
          </DialogContent>
          <DialogActions>
            {/*TODO: index to delete could be undefined? */}
            <Button
              onClick={this.props.closeAndDelete(this.props.indexToDelete)}
              color="primary"
            >
              Delete
            </Button>
            <Button onClick={this.props.handleCloseDelete} color="primary">
              Close
            </Button>
          </DialogActions>
        </Dialog>
      </Fragment>
    );
  }
}

export default withStyles(styles)(SecretList);
