import React from "react";
import ReactMarkdown from "react-markdown";
import guideContentPath from "./static/hardware_wallet_instructions.md";

const DefaultCodeRenderer = ReactMarkdown.renderers.code;
const DefaultBQRenderer = ReactMarkdown.renderers.blockquote;

const CodeRenderer = props => {
  return (
    <div
      className="bg-black-10 br2 pa2"
      style={{
        overflowY: "scroll",
        backgroundColor: "black",
        color: "white"
      }}
    >
      <DefaultCodeRenderer {...props} />
    </div>
  );
};

const BQRenderer = props => {
  return (
    <div
      className="bg-black-10 br2 pa2"
      style={{
        backgroundColor: "lightGray",
        padding: "10px"
      }}
    >
      <DefaultBQRenderer {...props} />
    </div>
  );
};

const GuideContentFunc = props => () => {
  return (
    <div style={{ paddingLeft: "10px", paddingRight: "10px" }}>
      <GuideContent {...props} />
    </div>
  );
};

class GuideContent extends React.Component {
  state = {
    terms: ""
  };

  componentWillMount() {
    fetch(guideContentPath)
      .then(response => response.text())
      .then(text => {
        this.setState({ terms: text });
      });
  }

  render() {
    const { classes } = this.props;

    return (
      <ReactMarkdown
        source={this.state.terms}
        renderers={{ code: CodeRenderer, blockquote: BQRenderer }}
      />
    );
  }
}

export default GuideContentFunc;
