import React, { Fragment } from "react";
import PropTypes from "prop-types";
import Button from "@material-ui/core/Button";
import { withStyles } from "@material-ui/core/styles";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import InfoIcon from "@material-ui/icons/Info";
import Tooltip from "@material-ui/core/Tooltip";
import TextField from "@material-ui/core/TextField";

const styles = theme => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit
  },
  dense: {
    marginTop: 16
  },
  menu: {
    width: 200
  },
  button: {
    margin: theme.spacing.unit
  }
});

class BeneficiarySecretView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false
    };
  }

  handleClickOpen = () => {
    this.setState({ open: true });
  };

  canDecryptSecret = () => {
    return (
      parseInt(this.props.secretProp.lastCheckIn, 16) * 1000 +
        this.props.convertJSONToMS(this.props.secretProp.timeBeforeReveal) <=
      this.props.currTs * 1000
    );
  };

  render() {
    const longText = `
                  The secret, which should already be encrypted for the beneficiary with PGP.
              `;

    const { classes } = this.props;

    const actionType = this.props.actionType;
    const shouldDisable = true;

    let secret = {};

    secret = this.props.secretProp;

    const secretUndefinedView = (
      <div style={{ padding: 20 }}>
        <strong>No secret selected. Please select an existing secret.</strong>
      </div>
    );

    if (secret === undefined || secret === {}) {
      return secretUndefinedView;
    }

    const blankName = secret["name"] === undefined || secret["name"] === "";

    // Necessary because TextField doesn't update if value is undefined.
    const toStr = key => {
      return key === undefined ? "" : key;
    };

    // Necessary because TextField doesn't update if value is undefined.
    const toStrOrZero = key => {
      return key === undefined ? "0" : key;
    };

    const tsToString = ts => {
      // TODO: why do I have to use radix 16 here but not in check in panel?
      let result = new Date(parseInt(ts, 16) * 1000);
      return result.toLocaleString();
    };

    return (
      <div>
        <div style={{ padding: 10, fontSize: "150%" }}>
          {actionType + " Secret"}
        </div>
        <Divider />
        <div>
          <div>
            <strong>{"Secret name: "}</strong>
            <Tooltip
              title={
                "Currently stored unencrypted for both testator and beneficiary."
              }
            >
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
            <div>
              <TextField
                id="outlined-multiline-static"
                className={classes.textField}
                disabled={shouldDisable}
                value={toStr(secret["name"])}
                margin="normal"
                variant="outlined"
                helperText={blankName ? "Error: Name cannot be blank" : ""}
                error={blankName}
              />
            </div>
          </div>
          <br />
          <div>
            <strong>{"Notes for beneficiary: "}</strong>
            <Tooltip title={"Currently public."}>
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
          </div>
          <div>
            <TextField
              id="outlined-multiline-static"
              multiline
              rows="4"
              value={toStr(secret["beneficiary_notes"])}
              className={classes.textField}
              disabled={shouldDisable}
              margin="normal"
              variant="outlined"
            />
          </div>
          <div>
            <strong>{"Time Delay: "}</strong>
            <Tooltip
              title={
                "A fixed amount of time from the last check in, before the secret can be viewed by the beneficiary. Currently public."
              }
            >
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
          </div>
          <div>
            {["days", "hours", "minutes", "seconds"].map(input => {
              return (
                <Fragment>
                  {input}
                  <TextField
                    id="outlined-multiline-static"
                    value={toStrOrZero(secret.timeBeforeReveal[input])}
                    className={classes.textField}
                    disabled={true}
                    margin="normal"
                    variant="outlined"
                    style={{ width: 50 }}
                    inputProps={{
                      maxLength: 2
                    }}
                  />
                </Fragment>
              );
            })}
          </div>
          <div>
            <strong>{"Last Check In: "}</strong>
            <Tooltip title={"The last time the testator checked in."}>
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
          </div>
          <div>
            <TextField
              id="outlined-multiline-static"
              value={tsToString(secret.lastCheckIn)}
              className={classes.textField}
              disabled={true}
              margin="normal"
              variant="outlined"
            />
          </div>
          <div>
            <Tooltip title={longText}>
              <strong>{"Secret: "}</strong>
            </Tooltip>
          </div>
          <div style={{ padding: 10 }}>
            <TextField
              id="outlined-multiline-static"
              multiline
              rows="4"
              value={toStr(secret.secret)}
              className={classes.textField}
              disabled={shouldDisable}
              margin="normal"
              variant="outlined"
            />
          </div>
          <div>
            <strong>{"Testator ETH address: "}</strong>
            <Tooltip title={"The secret owner's address."}>
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
            <div>
              <TextField
                id="outlined-multiline-static"
                value={toStr(secret.owner_addr)}
                className={classes.textField}
                disabled={shouldDisable}
                margin="normal"
                variant="outlined"
              />
            </div>
          </div>
          <Divider />
        </div>
        <div>
          <Button
            onClick={this.props.decryptSecret}
            className={classes.button}
            color="primary"
            variant={"contained"}
            disabled={!this.canDecryptSecret()}
          >
            Decrypt Secret
          </Button>
        </div>
        <div>
          <div style={{ padding: "10px" }}>
            This functionality simulates locking and unlocking the secret, since
            Enigma is not yet live to decrypt the secret.
            {!this.canDecryptSecret()
              ? " The time delay has not yet passed from the last check in."
              : " The time delay has passed and the secret can now be decrypted."}
          </div>
          {this.props.decryptedSecret === undefined ? (
            ""
          ) : (
            <div>
              <strong>Decrypted Secret:</strong>
              <div style={{ padding: 10 }}>
                <TextField
                  id="outlined-multiline-static"
                  multiline
                  rows="4"
                  value={this.props.decryptedSecret}
                  className={classes.textField}
                  disabled={shouldDisable}
                  margin="normal"
                  variant="outlined"
                />
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}

BeneficiarySecretView.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(BeneficiarySecretView);
